.btn
{
    background-color: var(--secundary);
    color: var(--font_color2);
    padding: 10px 30px;
    border-radius: 10px;
    font-family: var(--font);
    font-weight: 600;
    border: 1px solid var(--secundary);
    cursor: pointer;
}
.btn:hover
{
    background-color: var(--btn_hover)
}